    url('fonts/icomoon.ttf?e3p7y9') format('truetype'),
    url('fonts/icomoon.woff?e3p7y9') format('woff'),
    url('fonts/icomoon.svg?e3p7y9#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
